/** @jsxImportSource @emotion/react */
import React, { useEffect, useState } from 'react';
import fetchJsonp from "fetch-jsonp"
import {Button, TextField, Link} from '@material-ui/core';
import { css } from '@emotion/react'
import { styled } from '@mui/material/styles'
import { createTheme, ThemeProvider } from '@mui/material/styles';
import {indigo, red} from '@mui/material/colors'
import {ToStellarKnightsCcfolia} from "./to_charasheet_json/StellarKnights"
import VampireBloodSystemSort from './to_charasheet_json/VampireBloodSystemSorting';
import './App.css';

//type
type CharacterClipboardData = {
  kind: "character";
  data: Partial<Character>;
}

type Character = {
  name: string;
  memo: string;
  initiative?: number;
  externalUrl: string;
  status: {
    label: string;
    value: number;
    max: number;
  }[];
  params?: { label: string; value: string }[];
  iconUrl: string | null; // [!]
  faces: { iconUrl: string | null; label: string }[]; // [!]
  x: number; // [!]
  y: number; // [!]
  angle?: number;
  width?: number;
  height?: number;
  active: boolean; // [!]
  secret?: boolean;
  invisible?: boolean;
  hideStatus?: boolean;
  color?: string;
  commands?: string;
  owner?: string | null;
};

const darkTheme=createTheme({
  palette:{
    mode: 'dark',
  },
  typography:{
    fontFamily:[
      "Noto Sans JP",
      "Lato"
    ].join(",")
  }
});

const App:React.VFC=()=>{
  const [system,setSystem]=useState<string>("stellar")//state
  const [sheetId,setSheetId]=useState<string>("")//IDstate
  const [resCharaSheet,setResCharaSheet]=useState({})//APIJSONstate
  const [charaSheetUrl,setCharaSheetUrl]=useState<string>("")//URLstate

  //emotionCSS
  const inputForms=css`
    margin: 2% 0;
  `
  const inputId=css`
    width:80%;
  `

  const howToUseUl=css`
    width:70%;
    margin:0 auto;
  `

  const howToUseLi=css`
    list-style-type:demical;
    margin:1% 0;
  `

  const systemListLi=css`
    width:10%;
    margin:0 auto;
  `

  /*const spanEmphasis=css`
    color:red;
  `*/

  //React
  const InputForm:React.VFC=()=>{
    return (
      <div css={inputForms}>
        <form>
          {/*Material UITextField*/}
          <TextField variant="standard" label="URL" onChange={idHandleChange} value={charaSheetUrl} css={inputId} name="url" />
        </form>
      </div>
    )
  }

  const PageFooter:React.VFC=()=>{
    return (
      <footer>
        <p>:CC BY-NC-SA 4.0 NCYoutubeTRPG</p>
        <p>: <Link href="https://twitter.com/AmenoAtsuta" underline="hover">Twitter</Link> <Link href="https://www.youtube.com/channel/UCcNBKvsZBMPMJTwNaraELHg" underline="hover">Youtube</Link></p>
      </footer>
    )
  }

  //

  const handleSubmit=()=>{//submit
    console.log(sheetId)
    if(charaSheetUrl.includes("character-sheets.appspot.com") && sheetId!==""){//URLID
      console.log(`https://character-sheets.appspot.com/${system}/display?ajax=1&key=${sheetId}`)
      fetchJsonp(`https://character-sheets.appspot.com/${system}/display?ajax=1&key=${sheetId}`,{
        jsonpCallback: 'callback',
      })//JSONP
      .then((res)=>{return res.json()})//
      .then((json)=>{
        console.log(json);
        switch(system){//
          case "stellar"://
            setResCharaSheet(ToStellarKnightsCcfolia(json,sheetId))
            copyCharaSheetJson(ToStellarKnightsCcfolia(json,sheetId))//
            break
          default:
            alert("")
        }
      })
      .catch((err)=>{//(JSONP)
        console.log(err)
        alert("")
      })
    }else if(charaSheetUrl.includes("charasheet.vampire-blood.net") && sheetId!==""){
      fetchJsonp(`https://charasheet.vampire-blood.net/${sheetId}.js`,{
        jsonpCallback: 'callback',
      })
      .then((res)=>{return res.json()})//
      .then((json)=>{
        console.log(json)
        copyCharaSheetJson(VampireBloodSystemSort(json,sheetId))
      })
      .catch((err)=>{//(JSONP)
        console.log(err)
        alert("")
      })
    }
  }

  const copyCharaSheetJson=(res:CharacterClipboardData|null):void=>{//JSON
    if(!res){return}
    navigator.clipboard.writeText(JSON.stringify(res))//JSONstring
    .then(()=>{
      console.log('')
      alert("")
    })
    .catch((err)=>{console.log(err)})//
  }

  /*
  URL
  setSheetIdID()state
  state
  
  */
  useEffect(()=>{
    if(charaSheetUrl.includes("character-sheets.appspot.com")){
      setSheetId(charaSheetUrl.slice(charaSheetUrl.indexOf("key=")+4))//URLkey=ID
      setSystem(charaSheetUrl.slice(charaSheetUrl.indexOf("com/")+4,charaSheetUrl.indexOf("/",charaSheetUrl.indexOf("com/")+4)))//URL.com//
      //setSheetId(charaSheetUrl.slice(charaSheetUrl.indexOf("key=")+4))
      console.log(sheetId)
      console.log(system)
    }else if(charaSheetUrl.includes("charasheet.vampire-blood.net")){
      setSheetId(charaSheetUrl.slice(charaSheetUrl.indexOf("net/")+4))
    }
  },[charaSheetUrl])

  const idHandleChange=(event:React.ChangeEvent<HTMLInputElement>)=>{//React
    setCharaSheetUrl(event.target.value)
  }

  
  return (
    <ThemeProvider theme={darkTheme}>
      <div className="App">
        <h1></h1>
        <h2></h2>
        <p>
          <br/>
          (2ECoC)<br/>
          
        </p>
        <h2></h2>
        <h3></h3>
        <ul>
          <li css={systemListLi}></li>
        </ul>
        <h3></h3>
        <ul>
          <li css={systemListLi}>3rd</li>
        </ul>
        <h2></h2>
        <ul css={howToUseUl}>
          <li css={howToUseLi}>URL</li>
          <li css={howToUseLi}>"ID"URL</li>
          <li css={howToUseLi}>""""</li>
          <li css={howToUseLi}></li>
        </ul>
        <InputForm/>
        <Button variant="contained" onClick={handleSubmit} >
          
        </Button>
        <PageFooter/>
      </div>
    </ThemeProvider>
  );
}

export default App;
